<template>
  <view class="container">
    <!-- ÂèåÊâìÈòüÂëòÈÄâÊã© -->
    <view class="section">
      <text class="section-title">ÂèåÊâìÈòüÂëò</text>
      
      <!-- AÈòü -->
      <view class="team-section">
        <text class="team-label">AÈòü</text>
        <view class="team-players">
          <view class="player-item">
            <picker :value="selectedPlayerA1Index" :range="availablePlayersA1" @change="onPlayerA1Change">
              <view class="picker">
                <text>{{ playerA1 || 'ÈÄâÊã©ÈòüÂëò1' }}</text>
                <text class="arrow">‚ñº</text>
              </view>
            </picker>
          </view>
          
          <text class="and-text">&</text>
          
          <view class="player-item">
            <picker :value="selectedPlayerA2Index" :range="availablePlayersA2" @change="onPlayerA2Change">
              <view class="picker">
                <text>{{ playerA2 || 'ÈÄâÊã©ÈòüÂëò2' }}</text>
                <text class="arrow">‚ñº</text>
              </view>
            </picker>
          </view>
        </view>
      </view>

      <!-- VS -->
      <view class="vs-section">
        <text class="vs-text">vs</text>
      </view>

      <!-- BÈòü -->
      <view class="team-section">
        <text class="team-label">BÈòü</text>
        <view class="team-players">
          <view class="player-item">
            <picker :value="selectedPlayerB1Index" :range="availablePlayersB1" @change="onPlayerB1Change">
              <view class="picker">
                <text>{{ playerB1 || 'ÈÄâÊã©ÈòüÂëò3' }}</text>
                <text class="arrow">‚ñº</text>
              </view>
            </picker>
          </view>
          
          <text class="and-text">&</text>
          
          <view class="player-item">
            <picker :value="selectedPlayerB2Index" :range="availablePlayersB2" @change="onPlayerB2Change">
              <view class="picker">
                <text>{{ playerB2 || 'ÈÄâÊã©ÈòüÂëò4' }}</text>
                <text class="arrow">‚ñº</text>
              </view>
            </picker>
          </view>
        </view>
      </view>
    </view>

    <!-- ÊØîÂàÜËæìÂÖ• -->
    <view class="section">
      <text class="section-title">ÊØîÂàÜ</text>
      <view class="score-section">
        <view class="score-item">
          <text class="team-name">AÈòü</text>
          <view class="score-input">
            <input 
              type="number" 
              v-model="scoreA" 
              placeholder="0"
              @input="onScoreAInput"
            />
          </view>
        </view>
        
        <view class="vs-text">vs</view>
        
        <view class="score-item">
          <text class="team-name">BÈòü</text>
          <view class="score-input">
            <input 
              type="number" 
              v-model="scoreB" 
              placeholder="0"
              @input="onScoreBInput"
            />
          </view>
        </view>
      </view>
    </view>

    <!-- Êèê‰∫§ÊåâÈíÆ -->
    <view class="submit-section">
      <button 
        class="submit-btn" 
        :class="{ disabled: !canSubmit }" 
        @click="submitScore"
        :disabled="!canSubmit"
      >
        Êèê‰∫§ÂèåÊâìÊØîÂàÜ
      </button>
    </view>

    <!-- ‰ªäÊó•Â∑≤Êèê‰∫§ -->
    <view class="section">
      <text class="section-title">‰ªäÊó•Â∑≤Êèê‰∫§:</text>
      <view class="today-matches">
        <view v-if="todayMatches.length === 0" class="no-data">
          <text>ÊöÇÊó†Êï∞ÊçÆ</text>
        </view>
        <view v-else class="matches-list">
          <view class="matches-header">
            <text class="header-item">ÂØπÊàò</text>
            <text class="header-item">ÊØîÂàÜ</text>
            <text class="header-item">Êìç‰Ωú</text>
          </view>
          <view 
            v-for="match in todayMatches" 
            :key="match.id" 
            class="match-item"
          >
            <text class="match-teams">
              {{ match.teamA.join('&') }} vs {{ match.teamB.join('&') }}
            </text>
            <text class="match-score">{{ match.scoreA }} - {{ match.scoreB }}</text>
            <text class="match-action">üü¢</text>
          </view>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import { getPlayers, saveDoubleMatch, getDoubleMatches } from '@/utils/storage.js'

export default {
  data() {
    return {
      players: [],
      playerA1: '',
      playerA2: '',
      playerB1: '',
      playerB2: '',
      scoreA: '',
      scoreB: '',
      selectedPlayerA1Index: -1,
      selectedPlayerA2Index: -1,
      selectedPlayerB1Index: -1,
      selectedPlayerB2Index: -1,
      todayMatches: []
    }
  },
  
  computed: {
    selectedPlayers() {
      return [this.playerA1, this.playerA2, this.playerB1, this.playerB2].filter(p => p)
    },
    
    availablePlayersA1() {
      return this.players.filter(player => !this.selectedPlayers.includes(player) || player === this.playerA1)
    },
    
    availablePlayersA2() {
      return this.players.filter(player => !this.selectedPlayers.includes(player) || player === this.playerA2)
    },
    
    availablePlayersB1() {
      return this.players.filter(player => !this.selectedPlayers.includes(player) || player === this.playerB1)
    },
    
    availablePlayersB2() {
      return this.players.filter(player => !this.selectedPlayers.includes(player) || player === this.playerB2)
    },
    
    canSubmit() {
      return this.playerA1 && 
             this.playerA2 && 
             this.playerB1 && 
             this.playerB2 &&
             this.playerA1 !== this.playerA2 &&
             this.playerB1 !== this.playerB2 &&
             this.selectedPlayers.length === 4 &&
             new Set(this.selectedPlayers).size === 4 &&
             this.scoreA !== '' && 
             this.scoreB !== '' &&
             (parseInt(this.scoreA) > 0 || parseInt(this.scoreB) > 0)
    }
  },
  
  onLoad() {
    this.loadData()
  },
  
  onShow() {
    this.loadTodayMatches()
  },
  
  methods: {
    loadData() {
      this.players = getPlayers()
      this.loadTodayMatches()
    },
    
    loadTodayMatches() {
      const allMatches = getDoubleMatches()
      const today = new Date().toISOString().split('T')[0]
      this.todayMatches = allMatches.filter(match => match.date === today)
    },
    
    onPlayerA1Change(e) {
      this.selectedPlayerA1Index = e.detail.value
      this.playerA1 = this.availablePlayersA1[e.detail.value]
      this.updateAvailableIndexes()
    },
    
    onPlayerA2Change(e) {
      this.selectedPlayerA2Index = e.detail.value
      this.playerA2 = this.availablePlayersA2[e.detail.value]
      this.updateAvailableIndexes()
    },
    
    onPlayerB1Change(e) {
      this.selectedPlayerB1Index = e.detail.value
      this.playerB1 = this.availablePlayersB1[e.detail.value]
      this.updateAvailableIndexes()
    },
    
    onPlayerB2Change(e) {
      this.selectedPlayerB2Index = e.detail.value
      this.playerB2 = this.availablePlayersB2[e.detail.value]
      this.updateAvailableIndexes()
    },
    
    updateAvailableIndexes() {
      // Êõ¥Êñ∞ÂêÑ‰∏™ÈÄâÊã©Âô®ÁöÑÁ¥¢Âºï
      if (this.playerA1) {
        this.selectedPlayerA1Index = this.availablePlayersA1.indexOf(this.playerA1)
      }
      if (this.playerA2) {
        this.selectedPlayerA2Index = this.availablePlayersA2.indexOf(this.playerA2)
      }
      if (this.playerB1) {
        this.selectedPlayerB1Index = this.availablePlayersB1.indexOf(this.playerB1)
      }
      if (this.playerB2) {
        this.selectedPlayerB2Index = this.availablePlayersB2.indexOf(this.playerB2)
      }
    },
    
    onScoreAInput(e) {
      this.scoreA = e.detail.value
    },
    
    onScoreBInput(e) {
      this.scoreB = e.detail.value
    },
    
    submitScore() {
      if (!this.canSubmit) {
        return
      }
      
      const match = {
        teamA: [this.playerA1, this.playerA2],
        teamB: [this.playerB1, this.playerB2],
        scoreA: parseInt(this.scoreA),
        scoreB: parseInt(this.scoreB)
      }
      
      const result = saveDoubleMatch(match)
      if (result) {
        uni.showToast({
          title: 'Êèê‰∫§ÊàêÂäü',
          icon: 'success'
        })
        
        // ÈáçÁΩÆË°®Âçï
        this.resetForm()
        // Âà∑Êñ∞‰ªäÊó•ÊØîËµõ
        this.loadTodayMatches()
      } else {
        uni.showToast({
          title: 'Êèê‰∫§Â§±Ë¥•',
          icon: 'error'
        })
      }
    },
    
    resetForm() {
      this.playerA1 = ''
      this.playerA2 = ''
      this.playerB1 = ''
      this.playerB2 = ''
      this.scoreA = ''
      this.scoreB = ''
      this.selectedPlayerA1Index = -1
      this.selectedPlayerA2Index = -1
      this.selectedPlayerB1Index = -1
      this.selectedPlayerB2Index = -1
    }
  }
}
</script>

